export default {
  user: function(state) {
    return state.user
  },

  token(state) {
    return state.token
  },

  route(state) {
    return state.route
  },

  stateCode(state) {
    return state.stateCode
  },

  stateMsg(state) {
    return state.stateMsg
  }
}



